.container {
  background-color: rgba(224,235,232,0.8);
  padding: 20px 0px;
}

.content {
  width: 85%;
  max-width: 900px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0 auto;
}

.logo {
  width: 80px;
  height: 80px;
}

.navigation {
  width: 50%;
  max-width: 160px;
}

.navigationItem {
  font-size: 18px;
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.navigationItem:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.navigationList {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  list-style-type: none;
}

.navigationLink {
  text-decoration: none;
  color: #5B0B23;
  border: none;
}

@media screen and (max-width: 420px) {
  .navigation {
    max-width: 130px;
  }
}


